import { createRouter, createWebHistory } from "vue-router";
// createRouter - 라우터 인스턴스 함수
// createWebHistory - URL 히스토리모드를 사용하기 위한 함수
import HomePage from "../views/Home.vue";
import AboutPage from "../views/About.vue";
import ProductPage from "../views/Product.vue";

// 라우터 설정을 정의하는 배열 - path, name, component
const routes = [
  {
    path: '/',  // URL 경로 정의
    name: 'Home',  // 라우터 고유명 정의 - path의 경로를 참조하여 이동 시 사용
    component: HomePage  // 렌더링할 컴포넌트 지정
  },
  {
    path: '/about',
    name: 'About',
    component: AboutPage
  },
  {
    path: '/product',
    name: 'Product',
    component: ProductPage
  }
]

// createRouter - 라우터 인스턴스를 생성
// 라우터 인스턴스는 라우터의 경로와 연결된 컴포넌트를 관리하고 뷰를 어떻게 렌더링할지 결정함.
// history 모드는 URL에 "#"없이 깔끔한 URL 제공
// createWebHistory(process.env.BASE_URL) - history 모드 사용하여 URL 처리
// BASE_URL = public/index에서 지정된 기본 URL을 의미
// routes 배열을 createRouter 함수의 옵션으로 전달하여 라우터가 해당 경로들을 인식
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router;